import React, { useEffect, useState } from "react";
import { Alert, Form, Button, Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { useFormik } from "formik";
import CQAPI from "../api";

export const LoginForm = ({
	onSubmit,
	errors,
	setPasswordRequired,
	setUser,
}) => {
	const [error, setError] = useState("");
	const formic = useFormik({
		initialValues: { username: "" },
		onSubmit: (value) => {
			CQAPI.findUser(value.username)
				.then((resp) => {
					if (resp.passwordRequired) {
						setUser(resp);
						setPasswordRequired(true);
					}
					onSubmit(resp);
					console.log(resp);
				})
				.catch((err) => {
					// errors[0] = err.message;
					setError(err.message);
				});
		},
		validate: (value) => {
			let errors = {};
			if (!value.username) {
				errors.username = "Required";
			}
		},
	});
	return (

	<div class="loginCnt">
	<div class="col-md-12">
		<div class="row">
			<div class="col-md-6 displayNone">
				<div class="form-top">
					<img
					className="loginLogo"
					src="assets/CareerQuotient_large.jpeg"
					alt="Career Quotient Logo"
				/>
				</div>
			</div>
			<div class="col-md-12">
				<div class="form-bottom">
					<Form className="loginForm" onSubmit={formic.handleSubmit}>
			<h2 className="title loginText text-center">Login</h2>
			<p><b>Welcome to CQ.</b> Progress @ the speed of light</p>
		
			<div className="loginCTN">
			<Form.Group controlId="username">
				<Form.Control
					type="text"
					value={formic.values.username}
					onChange={formic.handleChange}
					onBlur={formic.handleBlur}
					placeholder="Enter Username/Email/Mobile number"
					required
				/>
			</Form.Group>
			{!error ? "" : <Alert variant="danger errorMsg">{error}</Alert>}
			{formic.errors.username ? (
				<Alert variant="danger errorMsg">{formic.errors.username}</Alert>
			) : null}
			</div>
				
			<div className="text-left">
				<Button type={"submit"} variant="primary loginBtn">
				Next
			</Button>
			<div className="wide-30">
				<p className="mb-1">Dont Have an Account?</p>
				<Link to="/sign-up">Sign Up</Link> 
			</div>
				{/* <p className="wide-30">New to CQ? <a href="#" className="cq-color text-bold">Take Assesment</a> first.</p> */}
			</div>
		
		</Form>
				</div>
			</div>
		</div>
	</div>
</div>		
	);
};

export const SignUpForm = (props) => {
	const formic = useFormik({
		initialValues: {
			firstName: "",
			lastName: "",
			mobile: "",
		},
		onSubmit: (values, user) => {
			props.onSubmit(values);
		},
		validate: (values) => {
			let errors = {};
			if (!values.firstName) {
				errors.firstName = "First name is required";
			} else if (
				/[\d`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
					values.firstName
				)
			) {
				errors.firstName = "Do not enter special characters";
			}
			if (!values.lastName) {
				errors.lastName = "Last name is required";
			} else if (
				/[\d `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
					values.lastName
				)
			) {
				errors.lastName = "Do not enter special characters";
			}
			if (!values.mobile) {
				errors.mobile = "Mobile number is required";
			} else if (!/[0-9]{10,10}/.test(values.mobile)) {
				errors.mobile = "Enter only 10 digit mobile number";
			}
			return errors;
		},
	});
	return (
		<div className="signUP">
		<Form
			autoComplete="new-password"
			noValidate
			onSubmit={formic.handleSubmit}
		>
		<h2 class="title loginText text-center">Sign Up</h2>

			<Form.Group controlId="firstName">
				
				<Form.Control
					autoComplete="off"
					value={formic.values.firstName}
					onChange={formic.handleChange}
					type="text"
					minLength={1}
					onBlur={formic.handleBlur}
					placeholder="First Name"
					required
				/>
				{formic.errors.firstName && formic.touched.firstName ? (
					<Alert variant="danger errorMsg">{formic.errors.firstName}</Alert>
				) : null}
				{/* <Form.Control.Feedback type="invalid">{errors.firstName}</Form.Control.Feedback> */}
			</Form.Group>
			<Form.Group controlId="lastName">
				
				<Form.Control
					autoComplete="on"
					value={formic.values.lastName}
					onBlur={formic.handleBlur}
					minLength={1}
					onChange={formic.handleChange}
					type="text"
					placeholder="Last Name"
					required
				/>
				{formic.errors.lastName && formic.touched.lastName ? (
					<Alert variant="danger errorMsg">{formic.errors.lastName}</Alert>
				) : null}
				{/* <Form.Control.Feedback type="invalid">{errors.lastName}</Form.Control.Feedback> */}
			</Form.Group>
			<Form.Group controlId="mobile">
				
				<Form.Control
					autoComplete="new-password"
					value={formic.values.mobile}
					onBlur={formic.handleBlur}
					onChange={formic.handleChange}
					type="text"
					placeholder="Mobile Number"
					required
				/>
				{formic.errors.mobile && formic.touched.mobile ? (
					<Alert variant="danger errorMsg">{formic.errors.mobile}</Alert>
				) : null}
				{/* <Form.Control.Feedback type="invalid">{errors.mobile}</Form.Control.Feedback> */}
			</Form.Group>
			<p>
				<small className="text-muted">
					One Time Password will be sent to the above entered mobile
					number
				</small>
			</p>
			<div className="wide-30">
			<Button type="submit" variant="primary loginBtn">
				Get OTP
			</Button>
			</div>
			{/* <p></p>
			<p>Already have an account?</p>
			<Link to="/login">Login</Link> */}
		</Form>
		</div>
	);
};

export const CreateAccountForm = (props) => {
	const [password, setPassword] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const [userName, setUserName] = useState({
		value: "",
		error: {},
		isFocused: false,
	});
	const handleUserNameChange = (e) => {
		const value = e.target.value;
		setUserName({
			...userName,
			value: value,
			error: {
				strLength: value.length >= 8 && value.length <= 30,
				character: /[a-z]/.test(value),
				number: /[0-9]/.test(value),
				specialCharacters: !/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(
					value
				),
			},
			isFocused: true,
		});
	};
	const handlePasswordChange = (e) => {
		const value = e.target.value;
		setPassword({
			...password,
			value: value,
			error: {
				strLength: value.length >= 8,
				character: /[a-z A-Z]/.test(value),
				number: /[0-9]/.test(value),
			},
			isFocused: true,
		});
	};
	const formic = useFormik({
		initialValues: {
			// firstName: '',
			// lastName: '',
			email: "",
			userName: "",
			password: "",
		},
		onSubmit: (values) => {
			values.userName = userName.value.toLowerCase();
			values.password = password.value;
			if (Object.values(userName.error).includes(false)) {
				setUserName({ ...userName, isFocused: true });
				return;
			}
			if (Object.values(password.error).includes(false)) {
				setPassword({ ...password, isFocused: true });
				return;
			}
			props.onSubmit(values);
		},
		validate: (values) => {
			let errors = {};
			// if (!values.firstName) {
			//     errors.firstName = 'First name is required'
			// } else if (/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.firstName)) {
			//     errors.firstName = 'Do not enter Special Character'
			// }
			// if (!values.lastName) {
			// 	errors.lastName = "Last name is required";
			// } else if (
			// 	/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.lastName)
			// ) {
			// 	errors.lastName = "Do not enter Special Character";
			// }
			if (!values.email) {
				errors.email = "email is required";
			} else if (
				!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
			) {
				errors.email = "Please provide valid email address";
			}
			return errors;
		},
	});
	return (		
		<div className="col-md-12">			
		<Form
			autoComplete="new-password"
			noValidate
			onSubmit={formic.handleSubmit}
		>
			{/* <ul className="list-unstyled">
				<li className="mb-4">
					<b>First Name: </b>
					{props.user.firstName || "Sample"}
				</li>
				<li className="mb-4">
					<b>Last Name: </b>
					{props.user.lastName || "Sample"}
				</li>
				<li className="mb-4">
					<b>Mobile:</b> {props.user.mobile.number || "Sample"}
				</li>
			</ul>
			<br /> */}
			
			
			<div className="row">
			<div className="col-md-6 leftBdr">
			<Form.Group controlId="firstName">
				<Form.Control
					autoComplete="off"
					type="text"
					minLength={1}
					placeholder="First Name"
					value={props.user.firstName}
					disabled
				/>
			</Form.Group>
			<Form.Group controlId="lastName">
				<Form.Control
					autoComplete="on"
					value={props.user.lastName}
					minLength={1}
					type="text"
					placeholder="Last Name"
					disabled
				/>
			</Form.Group>
			<Form.Group controlId="mobile">
				<Form.Control
					autoComplete="on"
					value={props.user.mobile.number}
					minLength={1}
					type="text"
					placeholder="Mobile"
					disabled
				/>
			</Form.Group>
			</div>
			<div className="col-md-6">
			<Form.Group controlId="email">
				
				<Form.Control
					autoComplete="new-password"
					value={formic.values.email}
					onBlur={formic.handleBlur}
					onChange={formic.handleChange}
					type="text"
					placeholder="Email Id"
					required
				/>
				{formic.errors.email && formic.touched.email ? (
					<Alert variant="danger errorMsg">{formic.errors.email}</Alert>
				) : null}
				{/* <Form.Control.Feedback type="invalid">{errors.email}</Form.Control.Feedback> */}
			</Form.Group>
			<Form.Group controlId="userName">
				{/* <div className="toolTip"> */}
				<Form.Control
					autoComplete="new-password"
					value={userName.value}
					onBlur={formic.handleBlur}
					onChange={handleUserNameChange}
					type="text"
					placeholder="Create a username"
					required
				/>
				{userName.isFocused ? (
					<div className="validation">
						<div className="validator">
							<i
								className={
									userName.error.strLength
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: userName.error.strLength
										? "green"
										: "red",
								}}
							>
								8-30 Characters
							</p>
						</div>
						<div className="validator">
							<i
								className={
									userName.error.number
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: userName.error.number
										? "green"
										: "red",
								}}
							>
								At least one number
							</p>
						</div>
						<div className="validator">
							<i
								className={
									userName.error.character
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: userName.error.character
										? "green"
										: "red",
								}}
							>
								At least one letter
							</p>
						</div>
						<div className="validator">
							<i
								className={
									userName.error.specialCharacters
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: userName.error.specialCharacters
										? "green"
										: "red",
								}}
							>
								No special characters
							</p>
						</div>
					</div>
				) : (
					""
				)}
			</Form.Group>
			<Form.Group controlId="password">
				
				<Form.Control
					autoComplete="new-password"
					value={password.value}
					onBlur={formic.handleBlur}
					onChange={handlePasswordChange}
					type="password"
					placeholder="Create a Password"
					required
				/>
				{password.isFocused ? (
					<div className="validation">
						<div className="validator">
							<i
								className={
									password.error.strLength
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: password.error.strLength
										? "green"
										: "red",
								}}
							>
								Minimum 8 characters
							</p>
						</div>
						<div className="validator">
							<i
								className={
									password.error.number
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: password.error.number
										? "green"
										: "red",
								}}
							>
								At least one number
							</p>
						</div>
						<div className="validator">
							<i
								className={
									password.error.character
										? "fas fa-check success error-icon"
										: "fas fa-times error error-icon"
								}
							></i>
							<p
								className="validation-item"
								style={{
									color: password.error.character
										? "green"
										: "red",
								}}
							>
								At least one letter
							</p>
						</div>
					</div>
				) : (
					""
				)}
			</Form.Group>
			
			{/* </div> */}
			</div>
			</div>
			<div className="text-center">
			<Button type="submit" variant="primary loginBtn">
				Create Account
			</Button>
			</div>
		</Form>

		</div>

	);
};

const calculateTimeLeft = () => {
	let start = 30;
	let time = 30 - 1;
	return start;
};

